<template>
	<nav id="box">
		<ul>
			<li>
				<router-link  exact to="/">博客</router-link>
				<router-link to="/add" exact>写博客</router-link>
				
			</li>
		</ul>
	</nav>
</template>

<script>
	export default{
		name:"blog"
	}
</script>

<style scoped>
	ul{
		list-style: none;
		text-align:center;
		margin:0;
	}
	li{
		display:inline-block;
		margin:0 10px;
	}
	a{
		color:#fff;
		text-decoration: none;
		padding:12px;
		border-radius: 5px;
	}
	nav{
		background: crimson;
		padding:30px 0;
		margin-bottom: 40px;
		width:800px;
		margin:40px auto;
		
	}
	.router-link-active{
		background: rgba(255,255,255,0.8);
		color:#444
	}
	
</style>


