<template>
	<div id="add-blog">
		<h2>添加博客</h2>
		<form v-show="!submitted">
			<label>博客标题</label>
			<input type="text" required v-model="blog.title"/>
			<label>博客内容</label>
			<textarea v-model="blog.content"></textarea>
			<div id="checkboxes">
				<label>Vue.js</label>
				<input type="checkbox" name=""  value="vue.js" v-model="blog.categories"/>
				<label>node.js</label>
				<input type="checkbox" name=""  value="node.js" v-model="blog.categories"/>
				<label>react.js</label>
				<input type="checkbox" name=""  value="react.js" v-model="blog.categories"/>
				<label>angular4</label>
				<input type="checkbox" name=""  value="angular4.js" v-model="blog.categories"/>
			</div>
			<label>博客作者：</label>  
			<select v-model="blog.author">
				<option v-for="item in authors">{{item}}</option>
			</select>
			<button @click.prevent="post1()">添加博客</button>
		</form>
		
		<div v-show="submitted">
			您的博客发送成功了!
		</div>
		
		<div id="preview">
			<h3>博客总览</h3>
			<p>博客标题：{{blog.title}}</p>
			<p>博客内容：</p>
			<p>{{blog.content}}</p>
			<p>博客分类：</p>
			<ul v-for="item in blog.categories">
				<li>{{item}}</li>
			</ul>
			<p>博客作者：</p>
			<p>{{blog.author}}</p>
		</div>
	</div>
</template>

<script>
	//@click.prevent可以直接组织刷新页面。
	export default{
		name:"addBlog",
		data(){
			return{
				blog:{
					title:"",
				    content:"",
				    categories:[],
				    author:""
				},
                authors:["张三","李四","王五","赵柳"],
                submitted:false
			}
		},
		methods:{
			post1(){
				
				//this.$http.post("地址"，“参数”，{emulateJSON: true}).then()
				//http://jsonplaceholder.typicode.com/posts
				//https://my-blog-b819d.firebaseio.com/
				//https://my-blog-b819d.firebaseio.com/posts.json

				var obj={
					"title":this.blog.title,
					"body":this.blog.content,
					"userId":1
				}
                this.$http.post("http://jsonplaceholder.typicode.com/posts",this.blog,{emulateJSON: true}).then(function(data){
                	console.log(data)
                	this. submitted=true;
                })
			}
		}
	}
</script>

<style scoped>
	*{
		margin:0px;
		padding:0px;
		list-style: none;
		box-sizing: border-box;
	}
	#add-blog{
		margin:20px auto;
		max-width:800px;
		padding:20px;
		background:#6677cc;
		
	}
	label{
		display:block;
		margin:20px 0 10px;
		text-align:left;
	}
	#checkboxes label{
		display:inline-block;
		
	}
	input[type=text],textarea,select{
		display:block;
		width:100%;
		padding:8px;
	}
	textarea{
		height:200px;
	}
    #checkboxes label{
   	    display:inline-block;
   	    margin-top:0;
    }
    #checkboxes input{
    	display:inline-block;
    	margin-right:10px;
    }
	button{
		display:block;
		margin:20px 0;
		background: crimson;
		color: #fff;
		border:0px;
		padding:14px;
		border-radius: 4px;
		font-size:18px;
		cursor: pointer;
	}
	#preview{
		padding:10px 20px;
		border:1px dotted #ccc;
		margin:30px 0px 
	}
	h3{
		margin-top:10px;
	}
</style>


