<template>
<div id="singleBlog">
	<div id="box">
		<h3 v-rainbow>{{blog.title}}</h3>
		
        <article>{{blog.body}}</article>
	</div>

</div>
</template>

<script>
	export default{
		name:"SingleBlog",
		data(){
			return {
				id:this.$route.params.id,
				blog:{}
			}
		},
		created(){
			this.$http.get("http://jsonplaceholder.typicode.com/posts/"+this.id)
			.then(
				function(data){
					console.log(data);
					this.blog=data.body;
				}
			)
		}
	}
</script>

<style scoped>
	#singleBlog{
		max-width:800px;
		margin:40px auto;
		background: #6677cc;
		text-align:center;
		padding:20px;
		box-sizing: border-box;
		border:1px dotted #ccc;
	}
	#box{
		background: #eee;
		box-sizing: border-box;
	}
	h3{
		padding :10px;
		box-sizing: border-box;
		color:#aaa;
		font-size:20px
	}
	article{
		padding:30px;
		box-sizing: border-box;
	}
</style>


