<template>
  <div id="show-blogs" v-theme:clumn="'narrow'">
  	
      <h1 v-theme:clumn="">博客总览</h1>
     <input type="text" v-model="search" placeholder="请输入内容" />
      <div v-for="blog in filt" class="single-blog">
      	 <router-link v-bind:to="'/blog/'+blog.id"><h2>{{blog.title | to-uppercase}}</h2></router-link>
      	 <section v-rainbow>{{blog.body | snop}}</section>
      	
      </div>
  </div>
</template>

<script>

export default {
  name: 'showBlogs',
  data(){
  	return {
  		  blogs:[],
  		  search:""
  	}
  },
  created(){
  	 //异地请求
  	 this.$http.get("http://jsonplaceholder.typicode.com/posts").then(function(data){
  	 	  console.log(data);
  	 	  this.blogs=data.body.slice(0,10)
  	 })
  	 /*
  	//本地请求
  	 this.$http.get("./../static/posts.json").then(function(data){
  	 	  console.log(data);
  	 	  this.blogs=data.body
  	 })*/
  },
  computed:{
  	"filt":function(){
  		 //es6里面有过滤
  		 //var newArr = arr.filter(item => item.indexOf('2')<0) 
  		 return this.blogs.filter((blog)=>{
  		 	   return blog.title.match(this.search);
  		 })
  		 	  
  		 
  	}
  },
  filters:{
  	"to-uppercase":function(value){
  		return value.toUpperCase().slice(0,20)+"..."
  	}
 
  }
}
</script>

<style scoped>
#show-blogs{
	max-width: 800px;
	margin:20px auto;
	text-align:center;
	padding-bottom: 20px;
  box-sizing: border-box;
}
.single-blog{
	padding:20px;
	margin:20px auto;
	box-sizing: border-box;
	background: #eee;
	width:700px;
}
#show-blogs a{
	color:#444;
	text-decoration: none;
}
input{
	   padding:8px ;
	   width:80%;
	   border:1px dotted #aaa;
	   box-sizing: border-box;
}
</style>



