// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import VueResource from 'vue-resource'
import VueRouter from 'vue-router'
//router这个是系统自己添加的路由文件
//import router from './router'
//这个是我们自己定义的路由文件
import Routes from './routes'
import ShowBlogs from "./components/ShowBlogs"
import AddBlog from "./components/AddBlog"


Vue.config.productionTip = false
Vue.use(VueResource)
Vue.use(VueRouter)
/* eslint-disable no-new */

let router=new VueRouter({
	routes:Routes,
	//[{path:"/",component:ShowBlogs},{path:"/add",component:AddBlog}],
	    
	mode:"history"
})




//自定义钩子指令
Vue.directive("rainbow",{
	 bind(el,binding,vnode){
	 	  el.style.color="#"+Math.random().toString(16).slice(2,8)
	 }
})


Vue.directive("theme",{
	 bind(el,binding,vnode){
	 	 if(binding.value=="width"){
	 	 	   el.style.maxWidth="1200px"
	 	 }else if(binding.value=="narrow"){
	 	 	   el.style.maxWidth="800px"
	 	 }
	 	 if(binding.arg=="clumn"){
	 	 	  el.style.background="#6677cc"
	 	 }
	 }
})
/*
Vue.filter("to-uppercase",function(value){
	  return value.toUpperCase().slice(0,20)+"..."
})
*/
//将上面的全局自定义过滤器换成局部自定义过滤器
Vue.filter("snop",function(value){
	  return value.slice(0,100)+"..."
})




new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



